import { getEmailUrl, getFacebookUrl, getRedditUrl, getXUrl } from "@rp/common/utils"
import { FacebookLogo, RedditLogo, XLogo } from "@rp/react/common/assets"
import { Box, Link, Typography } from "@rp/react/web/mui/core"
import { EmailOutlined } from "@rp/react/web/mui/icons"

type Props = {
  itemUrl: string
  content: string
  dataType: string
}

const SocialLink = ({
  label,
  icon,
  url,
}: {
  label: string
  icon: React.ReactNode
  url: string
}) => {
  return (
    <Link
      href={url}
      target="_blank"
      rel="noopener, noreferrer"
      sx={{
        display: "flex",
        flexBasis: "calc(50% - 16px)",
        p: 2,
        gap: 1,
        alignItems: "center",
        color: "text.primary",
        outline: "1px solid",
        outlineColor: "outline.border",
        borderRadius: 2,
        cursor: "pointer",

        "&:svg": {
          fill: "currentColor",
          stroke: "currentColor",
        },

        "&:hover": {
          textDecoration: "none",
          bgcolor: "blue.900",
          color: "primary.main",
          outlineColor: "primary.main",
        },
      }}
    >
      {icon}
      <Typography
        sx={{
          fontSize: 18,
          fontWeight: 700,
          letterSpacing: -0.25,
        }}
      >
        {label}
      </Typography>
    </Link>
  )
}

const ShareSocialLinks = ({ itemUrl, content, dataType }: Props) => {
  return (
    <Box>
      <Typography fontSize={18} fontWeight="bold" my={1.5}>
        Share on
      </Typography>
      <Box
        sx={{
          display: "flex",
          flexWrap: "wrap",
          gap: 2,
        }}
      >
        <SocialLink icon={<XLogo />} label="Twitter" url={getXUrl(itemUrl, content)} />
        <SocialLink icon={<FacebookLogo />} label="Facebook" url={getFacebookUrl(itemUrl)} />
        <SocialLink icon={<RedditLogo />} label="Reddit" url={getRedditUrl(itemUrl, content)} />
        <SocialLink
          icon={<EmailOutlined />}
          label="Email"
          url={getEmailUrl(`Shared ${dataType}`, `${content} ${itemUrl}`)}
        />
      </Box>
    </Box>
  )
}

export { ShareSocialLinks }
